@use "@pkg/react-storybook/Component";
@use "@app/bancor-components/Style/Layout/Position";

$name: Text;
$type: Component.$type-element;
$selector: Component.selector($name, $type);
$hidden-content-space: 1px;

@mixin noDrag {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    pointer-events: none;
}

@mixin hiddenText {
    @include Position.set($position: absolute);
    overflow: hidden;
    height: $hidden-content-space;
    width: $hidden-content-space;
}

:export {
    NAME_TEXT: $name;
    TYPE_TEXT: $type;
}
